.form-block {
	padding: 12px;
	background-color: $white;
	box-shadow: $box-shadow;

	@include media-breakpoint-up(sm) {
		padding: 25px;
	}
}

.form-control {
	height: 52px;

	@include media-breakpoint-up(md) {
		height: 64px;
	}

	@include media-breakpoint-up(xxl) {
		height: 78px;
	}

	&.is-invalid {
		color: $form-feedback-invalid-color;

		&:focus {
			box-shadow: $input-focus-box-shadow;
		}
	}

	&[disabled],
	&:disabled {
		@include placeholder {
			color: $gray-300;
		};

		color: $gray-300;
	}

	&[type="number"] {
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}

		-moz-appearance: textfield;
	}
}

textarea {
	&.form-control {
		height: 165px;
		padding-top: 16px;

		@include media-breakpoint-up(md) {
			padding-top: 20px;
		}
	}
}

.invalid-feedback {
	position: absolute;
	bottom: 1px;
	font-weight: 700;
	text-align: right;

	@include media-breakpoint-up(sm) {
		bottom: 5px;
	}

	@include media-breakpoint-down(sm) {
		font-size: 10px;
	}
}