.our-team-round-block {
	position: relative;

	@include media-breakpoint-down(md-extra) {
		max-width: 450px;
		margin: 0 auto;
	}

	@include media-breakpoint-up(xxl) {
		width: 860px;
	}

	&__photo-item {
		@include sizeElement(45px);

		position: absolute;
		display: block;
		border-radius: 50%;
		transition: background-color 0.3s;

		@include media-breakpoint-up(xs) {
			@include sizeElement(55px);
		}

		@include media-breakpoint-up(lg) {
			@include sizeElement(65px);
		}

		@include media-breakpoint-up(xl) {
			@include sizeElement(80px);
		}

		@include media-breakpoint-up(xxl) {
			@include sizeElement(115px);
		}

		&:before {
			@include absoluteCover;

			content: '';
			display: block;
			background: $gradient;
			border-radius: 50%;
			transition: opacity 0.3s;
			opacity: 0;
			z-index: 4;
		}

		&:hover {
			background-color: rgba($secondary, 0.2);
		}

		&.active {
			&:before {
				opacity: 1;
			}
		}

		&.top {
			top: -3%;
			left: 23%;

			@include media-breakpoint-up(xl) {
				top: -2%;
			}
		}

		&.top-left {
			top: 24%;
			left: -3%;

			@include media-breakpoint-up(xl) {
				left: -2%;
			}
		}

		&.bottom-left {
			bottom: 24%;
			left: -3%;

			@include media-breakpoint-up(xl) {
				left: -2%;
			}
		}

		&.bottom {
			bottom: -3%;
			left: 23%;

			@include media-breakpoint-up(xl) {
				bottom: -2%;
			}
		}
	}

	&__photo-item-inner {
		position: absolute;
		top: 5px;
		left: 5px;
		right: 5px;
		bottom: 5px;
		z-index: 5;
		border-radius: 50%;
		overflow: hidden;

		@include media-breakpoint-up(xxl) {
			top: 10px;
			left: 10px;
			right: 10px;
			bottom: 10px;
		}
	}
}