.slider {
	&-digital-network {
		position: relative;
		max-width: 228px;

		@include media-breakpoint-up(sm) {
			max-width: 650px;
		}

		@include media-breakpoint-up(lg) {
			max-width: 1100px;
		}

		@include media-breakpoint-up(xxl) {
			max-width: 1412px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 13px;
			left: -100vw;
			width: 300vw;
			height: 10px;
			background-color: $white;
			display: block;

			@include media-breakpoint-up(md) {
				top: 18px;
				height: 15px;
			}

			@include media-breakpoint-up(xxl) {
				top: 25px;
				height: 20px;
			}
		}

		.tns-ovh {
			overflow: inherit;
		}

		.tns-slider {
			display: flex;
			flex-wrap: wrap;
		}

		.tns-item {
			display: flex;
			width: 100%;
		}
	}

	&-our-team {
		.tns-controls {
			padding: 15px 15px 0 15px;
			border-top: 1px solid rgba($secondary, 0.1);
			margin: 30px -15px 0;

			@include media-breakpoint-up(sm) {
				padding: 15px 30px 0 30px;
				margin: 30px -30px 0;
			}

			@include media-breakpoint-up(xxl) {
				padding: 40px 25px 0 25px;
				margin: 50px -50px 0;
			}

			[data-controls="prev"],
			[data-controls="next"] {
				@include sizeElement(50px);
				@include inlineFlexCenter;

				position: relative;
				border-radius: 50%;
				border: none;
				color: $white;
				padding: 10px;
				font-size: 18px;
				transition: color 0.3s;

				@include media-breakpoint-up(xxl) {
					@include sizeElement(62px);

					font-size: 20px;
				}

				&:before {
					@include absoluteCover;

					content: '';
					display: block;
					background: $gradient-hover;
					border-radius: 50%;
					z-index: 1;
					transition: opacity 0.3s;
				}

				&:after {
					@include absoluteCover;

					content: '';
					display: block;
					background: $gradient;
					border-radius: 50%;
					z-index: 2;
					transition: opacity 0.3s;
				}

				> * {
					position: relative;
					z-index: 5;
				}

				&[disabled] {
					background-color: rgba($secondary, 0.1);
					color: $blue;
					pointer-events: none;

					&:before,
					&:after {
						opacity: 0;
					}
				}

				&:hover {
					&:after {
						opacity: 0;
					}
				}
			}

			[data-controls="prev"] {
				margin-right: 15px;
			}
		}
	}
}