.cookies-banner {
	position: fixed;
	bottom: 10px;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: $zindex-fixed;
	width: 100%;
	pointer-events: none;

	@include media-breakpoint-up(md) {
		bottom: 20px;
	}

	@include media-breakpoint-up(lg) {
		bottom: 45px;
	}

	&__title {
		font-weight: 400;
		font-family: inherit;
		font-size: 16px;
		margin-bottom: 5px;

		@include media-breakpoint-up(sm) {
			font-size: 18px;
		}

		@include media-breakpoint-up(md) {
			font-size: 22px;
		}
	}

	&__text {
		@include media-breakpoint-down(md) {
			font-size: 12px;
		}
	}

	&__main-content {
		position: relative;
		border-radius: $border-radius-xs;
		background-color: $white;
		box-shadow: $box-shadow;
		padding: 15px 15px 15px 65px;
		pointer-events: auto;

		@include media-breakpoint-up(md) {
			display: flex;
			justify-content: space-between;
			padding: 25px 25px 25px 105px;
		}

		@include media-breakpoint-up(lg) {
			align-items: center;
		}

		&:before {
			content: "\e909";
			position: absolute;
			top: 20px;
			left: 18px;
			font-family: icomoon;
			display: inline-block;
			font-size: 29px;
			line-height: 1;
			background: $gradient;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;

			@include media-breakpoint-up(md) {
				font-size: 45px;
				top: 40px;
				left: 30px;
			}
		}
	}

	&__text-block {
		margin-bottom: 20px;

		@include media-breakpoint-up(md) {
			padding-right: 40px;
			margin-bottom: 0;
		}

		@include media-breakpoint-up(lg) {
			max-width: 650px;
			padding-right: 60px;
		}
	}

	&__actions {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-left: -40px;

		@include media-breakpoint-up(xs) {
			margin-left: -15px;
		}

		@include media-breakpoint-up(sm) {
			margin-left: 0;
		}

		@include media-breakpoint-up(md) {
			flex: 0 0 auto;
			width: auto;
			max-width: 100%;
			justify-content: flex-end;
			flex-direction: column-reverse;
		}

		@include media-breakpoint-up(lg) {
			flex-direction: row;
		}

		> * {
			&:not(:last-child) {
				margin-right: 20px;

				@include media-breakpoint-up(xs) {
					margin-right: 40px;
				}

				@include media-breakpoint-up(md) {
					margin-top: 25px;
					margin-right: 0;
				}

				@include media-breakpoint-up(lg) {
					margin-right: 30px;
					margin-top: 0;
				}

				@include media-breakpoint-up(xxl) {
					margin-right: 40px;
				}
			}
		}
	}
}